interface IUser {
    name: string;
    userAddress: string;
}

export class User {
  name: string;
  userAddress: string;

  constructor(obj: IUser = {name:'',  userAddress:''}) {
      this.name = obj.name;
      this.userAddress = obj.userAddress;
  };
}



