interface IAnswer {
    id: any;
    questionId: any;
    answer: string;
    votes: number;
}

export class Answer {
  id: any;
  questionId: any;
  answer: string;
  votes: number;

  constructor(obj: IAnswer = {id:0, questionId:'',  answer:'', votes: 0}) {
      this.id = String(obj.id);
      this.questionId = String(obj.questionId);
      this.answer = obj.answer;
      this.votes = obj.votes;
  };
}



