import { Injectable } from '@angular/core';
import { Answer } from './answer.class';
import { Question } from './question.class';

@Injectable()
export class QuestionService {

  questions: Question[];

	constructor() {
        this.questions = [];
	}

  addQuestion(args: any) {
     let q = new Question({
         id: args.id,
         title: args.title,
         description: args.description,
         tags: args.tags.split(','),
         votes: 0,
         views: 0,
         answers: 0,
         address: args.user
     });
     if (this.questions.filter(e => e.id == args.id).length == 0) {
       this.questions.unshift(q);
     }
     return this.questions;
  }

  updateQuestionAnswers(i: number, answers: number): number {
    this.questions[i].answers = answers;
    return this.questions[i].answers;
  }

  updateQuestionViews(i: number, views: number): number {
    this.questions[i].views = views;
    return this.questions[i].views;
  }

  updateQuestionVotes(i: number, votes: number): number {
      this.questions[i].votes = votes;
      return this.questions[i].votes;
  }

  updateCurrentVotes(id: number, votes: number): number {
    for (let i in this.questions) {
      if (this.questions[i].id == id) {
        this.questions[i].views = votes;
        return votes;
      }
    }
  }

  async getQuestions(dappOverflow) {
    var questions = [];
    console.log('Getting questions');
    try {
      const deployedDappOverflow = await dappOverflow.deployed();
      const dappOverflowQuestions = await deployedDappOverflow.getQuestions();
      var questionsLen = dappOverflowQuestions.length;
      for (var i=0; i<dappOverflowQuestions.length; i++) {
        const dappOverflowQuestion = await deployedDappOverflow.getQuestion(dappOverflowQuestions[i]);
				let q1 = new Question({
            id: dappOverflowQuestions[i],
            title: dappOverflowQuestion[0],
            description: dappOverflowQuestion[1],
            tags: dappOverflowQuestion[2].split(','),
            votes: dappOverflowQuestion[3],
            views: dappOverflowQuestion[4],
            answers: dappOverflowQuestion[5],
            address: dappOverflowQuestion[6]
        });
				questions.unshift(q1);
      }
      this.questions = questions;
    } catch (e) {
      console.log(e);
    }
    return questions;
  }

  async getAnswers(id, length, dappOverflow) {
    var answers = [];
    console.log('Getting answers');
    try {
      const deployedDappOverflow = await dappOverflow.deployed();
      const dappOverflowAnswers = await deployedDappOverflow.getAnswers(id, parseInt(length));
      for (var i=0; i<dappOverflowAnswers.length; i++) {
        const dappOverflowAnswer = await deployedDappOverflow.getAnswer(parseInt(dappOverflowAnswers[i]));
        let a = new Answer({
            id: dappOverflowAnswers[i],
            questionId: id,
            answer: dappOverflowAnswer[0],
            votes: dappOverflowAnswer[1]
        });
        answers.unshift(a);
      }
      return answers;
    } catch (e) {
      console.log(e);
    }
    return answers;
  }

  getTaggedQuestions(tag): Question[] {
		return this.questions.filter(function(q){
      if(q.tags.indexOf(tag) !== -1){
          return q;
      }
    });
	}

  stringToArray(tags: string): string[] {
    tags = tags.replace(/\s/g,'');
    if(tags.indexOf(",") !== -1){
      return tags.split(',');
    } else {
      return [tags];
    }
  }

    async getQuestion(id: number, dappOverflow) {
      try {
        const deployedDappOverflow = await dappOverflow.deployed();
        const dappOverflowQuestion = await deployedDappOverflow.getQuestion(id);
				let q = new Question({
            id: id,
            title: dappOverflowQuestion[0],
            description: dappOverflowQuestion[1],
            tags: dappOverflowQuestion[2].split(','),
            votes: dappOverflowQuestion[3],
            views: dappOverflowQuestion[4],
            answers: dappOverflowQuestion[5],
            address: dappOverflowQuestion[6]
        });
        return q;
      } catch (e) {
        console.log(e);
        return new Question();
      }
	}

  hex2a(hexx) {
      var hex = hexx.toString().replace(/0+$/,'').replace(/^0x+/, '');//force conversion
      var str = '';
      for (var i = 0; i < hex.length; i += 2)
          str += String.fromCharCode(parseInt(hex.substr(i, 2), 16));
      return str;
  }
}



